.cover {
  background: url("../../../build/images/cover-inicio.webp") no-repeat top center;
  background-size: cover;
  height: 300px;
  width: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.cover::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: "";
  background-color: rgba(0, 0, 0, 0.4);
}
.cover__title {
  z-index: 1;
  position: relative;
  text-align: center;
  color: #fff;
}
@media (min-width: 768px) {
  .cover {
    height: 400px;
  }
}
@media (min-width: 1024px) {
  .cover {
    height: 600px;
  }
}

@media all and (min-width: 400px) {
  .cover {
    padding: 100px 0;
  }
}
.cont-page-about .cont-order__first .titleblue {
  color: rgba(var(--primary-color));
  font-size: 1.5rem;
}
.cont-page-about .cont-order__first .formatted-text {
  color: rgba(var(--text-color));
  font-size: 1.2rem;
}
.cont-page-about .cont-order__first .titleyellow {
  color: rgba(var(--primary-complement));
  font-size: 1.2rem;
}

@media (min-width: 768px) {
  .cont-order {
    display: grid;
    grid-template-columns: 50% 50%;
  }
  .cont-order__first {
    order: 0;
  }
  .cont-order__second {
    order: 2;
  }
  .cont-order__third {
    order: 1;
  }
}